import React, { useEffect, useRef, useState } from 'react'
import { Link, useNavigate, useOutletContext, useParams } from 'react-router-dom'
import { useSignupMutation } from './authApiSlice'

const Signup = () => {
  const { referrer } = useParams()
  const navigate = useNavigate()
  const [signup, { isLoading }] = useSignupMutation()
  const errRef = useRef()
  const firstNameRef = useRef()
  const [errMsg, setErrMsg] = useState('')
  const [firstName, setFirstName] = useState('')
  const [lastName, setLastName] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [confpassword, setConfpassword] = useState('')

  const { setTitle, setBody, setVariant, setShow } = useOutletContext()

  useEffect(() => {
    firstNameRef.current.focus()
  }, [])

  useEffect(() => {
    setErrMsg('')
  }, [firstName, lastName, email, password, confpassword])


  const referrerInput = referrer
    ? <div className="col-12">
      <label htmlFor="referrer">Referrer</label>
      <input type="text" value={referrer} name="referrer" className="form-control" id="referrer" placeholder='Referrer' readOnly />
    </div>
    : null

  async function handleSubmit(e) {
    e.preventDefault()
    const signupData = {
      firstName,
      lastName,
      email,
      password,
      rePassword: confpassword,
      referrer: referrer,
    }
    try {
      const { message } = await signup(signupData).unwrap()
      setEmail('')
      setShow(true)
      setTitle('SUCCESS')
      setBody(`${message}. Log in to proceed`)
      setVariant('success')
      setTimeout(() => { navigate('/login') }, 3000)
    } catch (error) {
      if (error.data.errors) {
        setShow(true)
        setTitle('ERROR')
        setBody(error.data.errors[0]?.msg)
        setVariant('warning')
      } else {
        setShow(true)
        setTitle('ERROR')
        setBody(error.data.message)
        setVariant('warning')
      }
    }
  }

  const errClass = errMsg ? "errmsg" : "offscreen"

  return (
    <>
      <div className="pt-4 pb-2">
        <h5 className="card-title text-center pb-0 fs-4">Sign up</h5>
      </div>

      <form className="row g-3 needs-validation" onSubmit={handleSubmit}>
        <p ref={errRef} className={errClass} aria-live="assertive">{errMsg}</p>

        <div className="col-12 form-floating">
          <input ref={firstNameRef} onChange={e => { setFirstName(prev => e.target.value) }} value={firstName} type="text" name="firstName" className="form-control" id="firstName" placeholder="First Name" required />
          <label htmlFor="firstName">First Name</label>
        </div>

        <div className="col-12 form-floating">
          <input onChange={e => { setLastName(prev => e.target.value) }} value={lastName} type="text" name="lastName" className="form-control" id="lastName" placeholder="Last Name" required />
          <label htmlFor="lastName">Last Name</label>
        </div>

        <div className="col-12 form-floating">
          <input onChange={e => { setEmail(prev => e.target.value) }} value={email} type="email" name="email" className="form-control" id="yourEmail" placeholder="Email Address" required />
          <label htmlFor="yourEmail">Email Address</label>
        </div>

        <div className="col-12 form-floating">
          <input onChange={e => { setPassword(prev => e.target.value) }} value={password} type="password" name="password" className="form-control" id="yourPassword" placeholder="Password" required />
          <label htmlFor="yourPassword">Password</label>
        </div>

        <div className="col-12 form-floating">
          <input onChange={e => { setConfpassword(prev => e.target.value) }} value={confpassword} type="password" name="confpassword" className="form-control" id="confPassword" placeholder="Confirm Password" required />
          <label htmlFor="confPassword">Confirm Password</label>
        </div>

        {referrerInput}

        <div className="col-12">
          <button className="btn btn-dark w-100 mb-3" type="submit" disabled={isLoading}>
            {isLoading ? 'Loading...' : 'Sign up'}
          </button>
          <button className="btn btn-outline-dark w-100">
            <span><i className="bi bi-google"></i> </span>Sign up with Google
          </button>
        </div>

        <div className="col-12">
          <p className="small mb-0">Already have an account? <Link to="/login">Log in</Link></p>
        </div>
      </form>
    </>
  )
}

export default Signup