import React, { useState } from 'react'
import { Link, Outlet } from 'react-router-dom'
import ToastComponent from './ToastComponent'

const PublicLayout = () => {

    const [title, setTitle] = useState('')
    const [body, setBody] = useState('')
    const [variant, setVariant] = useState('')
    const [show, setShow] = useState(false)

    return (
        <main className="form-page">
            <ToastComponent show={show} setShow={setShow} title={title} body={body} variant={variant} />
            <div className="container">
                <section className="section register min-vh-100 d-flex flex-column align-items-center justify-content-center py-4">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-lg-5 col-md-6 d-flex flex-column align-items-center justify-content-center">
                                <div className="d-flex justify-content-center py-4">
                                    <Link to="#" className="logo d-flex align-items-center w-auto">
                                        <img src="/assets/img/logo-white.png" alt="" />
                                        <span className="d-none d-lg-block" id="logo-white">Marstrade</span>
                                    </Link>
                                </div>
                                <div className="card mb-3">
                                    <div className="card-body">
                                        <Outlet context={{ setTitle, setBody, setVariant, setShow }} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        </main>
    )
}

export default PublicLayout