import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { selectDashboard } from '../dashboard/dashSlice'
import { selectDeposit, setDeposit } from './depositSlice'

function CheckoutForm() {
  const depositData = useSelector(state => selectDeposit(state))
  const navigate = useNavigate()
  useEffect(() => {
    if (!depositData) navigate('/deposit-funds')
  }, [navigate, depositData])

  const { owner } = useSelector(state => selectDashboard(state))
  const [email, setEmail] = useState('')
  useEffect(() => {
    setEmail(prev => owner?.email)
  }, [owner])

  const dispatch = useDispatch()
  const [loading, setLoading] = useState(false)
  const handleSubmit = (e) => {
    e.preventDefault()
    const payData = {
      email,
      payMethod: e.target.payMethod.value,
    }
    dispatch(setDeposit(payData))
    setLoading(prev => true)
    setTimeout(() => {
      navigate('/payment')
    }, 1000)
  }

  return (
    <section className="section">
      <div className="row">
        <div className="col-lg-6">
          <div className="card">
            <div className="card-body">
              <Link to="/deposit-funds"><i className="bi bi-arrow-left-short"></i>Back</Link>
              <p className="card-title">Pay</p>
              <h4 className="mb-3">${depositData?.amount}</h4>

              <hr />

              <div className="row mb-3">
                <div className="col-lg-6 col-md-4 label ">Investment Plan</div>
                <div className="col-lg-3 col-md-4">{depositData?.invPlan}</div>
              </div>

              <div className="row mb-3">
                <div className="col-lg-6 col-md-4 label">Investment Package</div>
                <div className="col-lg-3 col-md-4">{depositData?.invPackage}</div>
              </div>

              <div className="row mb-3">
                <div className="col-lg-6 col-md-4 label">Investment Duration</div>
                <div className="col-lg-3 col-md-4">7days</div>
              </div>

              <div className="row mb-3">
                <div className="col-lg-6 col-md-4 label">ROI</div>
                <div className="col-lg-3 col-md-4">2.5% daily</div>
              </div>
            </div>
          </div>
        </div>

        <div className="col-lg-6">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">Payment information</h5>

              <form onSubmit={handleSubmit}>
                <div className="col-12 mb-3 form-floating">
                  <input onChange={e => setEmail(e.target.value)} value={email} name="email" type="email" className="form-control" id="email" placeholder="Email Address" required />
                  <label htmlFor="email" className="col-md-4 col-lg-3 col-form-label">Email Address</label>
                </div>

                <div className="col-12 mb-3">
                  <select className="form-select" name="payMethod" id="payMethod" required>
                    <option value="">Select a Payment method</option>
                    <option value="btc">Bitcoin</option>
                    <option value="eth">Ethereum</option>
                    <option value="usdt">USDT</option>
                  </select>
                </div>

                <div className="col-12">
                  <button disabled={loading} className="btn btn-dark w-100 mb-3" type="submit">{loading ? 'Processing...' : `Pay $${depositData?.amount}`}</button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}

export default CheckoutForm