import React from 'react'
import { Link } from 'react-router-dom'

const Sidebar = ({ active }) => {

    const content = (
        <aside id="sidebar" className="sidebar">
            <ul className="sidebar-nav" id="sidebar-nav">

                <li className="nav-item">
                    <Link className={`nav-link ${active === 'dashboard' ? 'active' : ''}`} to="/">
                        <i className="bi bi-grid"></i>
                        <span>Dashboard</span>
                    </Link>
                </li>

                <li className="nav-item">
                    <Link className={`nav-link ${active === 'deposit' ? 'active' : ''}`} to="/deposit-funds">
                        <i className="bi bi-upload"></i>
                        <span>Deposit funds</span>
                    </Link>
                </li>

                <li className="nav-item">
                    <Link className={`nav-link ${active === 'withdrawal' ? 'active' : ''}`} to="/withdraw-funds">
                        <i className="bi bi-download"></i>
                        <span>Withdrawal</span>
                    </Link>
                </li>

                <li className="nav-item">
                    <Link className={`nav-link ${active === 'history' ? 'active' : ''}`} to="/history">
                        <i className="bi bi-receipt"></i>
                        <span>Transaction History</span>
                    </Link>
                </li>

                <li className="nav-item">
                    <Link className={`nav-link ${active === 'profile' ? 'active' : ''}`} to="/account-profile">
                        <i className="bi bi-person"></i>
                        <span>Profile</span>
                    </Link>
                </li>
            </ul>
        </aside>
    )
    return content
}

export default Sidebar