import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useNavigate, useOutletContext } from 'react-router-dom'
import { selectDashboard } from '../dashboard/dashSlice'
import { useMakeDepositMutation } from './depositApiSlice'
import { clearDeposit, selectDeposit } from './depositSlice'
import { paylogic } from './paymentLogic'

function Payment() {

    const { setShow, setVariant, setTitle, setBody } = useOutletContext()
    const [makeDeposit, { isLoading }] = useMakeDepositMutation()
    const dash = useSelector(state => selectDashboard(state))

    const depositData = useSelector(state => selectDeposit(state))
    const payMethod = depositData?.payMethod
    const pay = dash?.[payMethod]

    const navigate = useNavigate()
    useEffect(() => {
        if (!depositData) navigate('/deposit-funds')
    }, [navigate, depositData])

    const { type, logo } = paylogic[payMethod] ?? {}

    const dispatch = useDispatch()
    const handleSubmit = async (e) => {
        e.preventDefault()
        const payload = new FormData()
        payload.append('receipt', e.target.receipt.files[0])
        payload.append('userId', dash?.owner.id)
        payload.append('email', depositData?.email)
        payload.append('plan', depositData?.invPlan)
        payload.append('amount', depositData?.amount)
        payload.append('invPackage', depositData?.invPackage)

        try {
            const message = await makeDeposit({ payload, dashId: dash?.id }).unwrap()

            setShow(true)
            setVariant('success')
            setTitle('SUCCESS')
            setBody(message)

            e.target.receipt.value = null
            dispatch(clearDeposit())
        } catch (error) {
            setShow(true)
            setVariant('warning')
            setTitle('ERROR')
            setBody(error.data.message)
        }
    }

    return (
        <section className="section">
            <div className="row">
                <div className="col-lg-6">
                    <div className="card">
                        <div className="card-body">
                            <Link to="/checkout"><i className="bi bi-arrow-left-short"></i>Back</Link>
                            <p className="card-title">Pay</p>
                            <h4 className="mb-3">${depositData?.amount}</h4>

                            <hr />

                            <div className="row mb-3">
                                <div className="col-lg-6 col-md-4 label ">Investment Plan</div>
                                <div className="col-lg-3 col-md-4">{depositData?.invPlan}</div>
                            </div>

                            <div className="row mb-3">
                                <div className="col-lg-6 col-md-4 label">Investment Package</div>
                                <div className="col-lg-3 col-md-4">{depositData?.invPackage}</div>
                            </div>

                            <div className="row mb-3">
                                <div className="col-lg-6 col-md-4 label">Investment Duration</div>
                                <div className="col-lg-3 col-md-4">7days</div>
                            </div>

                            <div className="row mb-3">
                                <div className="col-lg-6 col-md-4 label">ROI</div>
                                <div className="col-lg-3 col-md-4">2.5% daily</div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="col-lg-6">
                    <div className="card">
                        <div className="card-body">
                            <div className="d-flex justify-content-center py-4">
                                <h2 className="logo d-flex align-items-center w-auto">
                                    <img src={logo} alt={`${type} logo`} width="40px" height="40px" />
                                    <span>{type} Payment</span>
                                </h2>
                            </div>
                            <div className=" text-center">
                                <p>To complete payment, please send ${depositData?.amount} to the address below</p>
                                <img className="text-center" src={pay?.barcode} alt="wallet barcode" width="250px" height="250px" />
                                <p>{pay?.address} <span><button className="btn btn-outline-secondary action-btn"><i className="bi bi-clipboard"></i></button></span></p>
                            </div>
                            <form onSubmit={handleSubmit} className='text-center'>
                                <p className="mb-3">Upload a proof of payment here. After verification, your wallet will be credited.</p>
                                <input name='receipt' className="mb-3 form-control" type="file" accept='image/*' required />
                                <button disabled={isLoading} className="w-80 btn btn-dark" type="submit">{isLoading ? 'Sending...' : 'Submit'}</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Payment