import React, { useEffect, useRef, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useNavigate, useOutletContext } from 'react-router-dom'
import { plans } from './DepositLogic'
import { setDeposit } from './depositSlice'

const DepositForm = () => {

  const { setActive } = useOutletContext()
  useEffect(() => {
    setActive(prev => 'deposit')
  }, [setActive])

  const amount = useRef()
  const invPackage = useRef()
  const [invPlan, setInvPlan] = useState('')
  const [min, setMin] = useState(0)
  const [max, setMax] = useState(0)
  useEffect(() => {
    setMin(prev => plans[invPlan]?.min || 500)
    setMax(prev => plans[invPlan]?.max)
  }, [invPlan])

  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [loading, setLoading] = useState(false)
  const handleSubmit = (e) => {
    e.preventDefault()
    const depositData = {
      invPlan,
      invPackage: invPackage.current.value,
      amount: amount.current.value,
    }
    dispatch(setDeposit(depositData))
    setLoading(prev => true)
    setTimeout(() => {
      navigate('/checkout')
    }, 1000)
  }

  const planOptions = Object.keys(plans).map(plan =>
    <option key={plan} value={plan}>{plan}</option>
  )

  const packageOptions = invPlan ? Object.entries(plans[invPlan]?.packages).map(([key, val]) =>
    <option key={key} value={key}>{val}</option>
  ) : null

  return (
    <>
      <div className="pagetitle">
        <h1>Deposit funds</h1>
      </div>

      <section className="section">
        <div className="app-content pt-3 p-md-3 p-lg-4">
          <div className="container-xl">
            <div className="row g-4">
              <div className="col-12 col-md-4">
                <p>Select the investment plan and package, enter the amount you want to invest and proceed to the secured payment portal.</p>
              </div>
              <div className="col-12 col-md-8">
                <div className="app-card app-card-settings shadow-sm p-4">

                  <div className="app-card-body">
                    <form onSubmit={handleSubmit}>
                      <div className="mb-3">
                        <label htmlFor="payment-method" className="form-label">Investment plan</label>
                        <select onChange={e => setInvPlan(prev => e.target.value)} id="paymetho-method" className="form-select" required>
                          <option defaultValue value="">Click to select</option>
                          {planOptions}
                        </select>
                      </div>

                      <div className="mb-3">
                        <label htmlFor="payment-method" className="form-label">Investment package</label>
                        <select ref={invPackage} id="paymetho-method" className="form-select" required>
                          <option defaultValue value="">Click to select</option>
                          {packageOptions}
                        </select>
                      </div>
                      <div className="mb-3">
                        <label htmlFor="setting-input-1" className="form-label">Investment amount</label>
                        <input ref={amount} type="number" className="form-control" placeholder="Amount in USD($)" min={min} max={max} required />
                      </div>
                      <button disabled={loading} type="submit" className="w-100 btn btn-dark">{loading ? 'Processing...' : 'Proceed'}</button>
                    </form>
                  </div>

                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default DepositForm