import React, { useEffect } from 'react'
import { useOutletContext } from 'react-router-dom'
import useAuth from '../../hooks/useAuth'
import { useGetDashboardQuery } from './dashboardApiSlice'
import { getDashData } from './DashLogic'

const Dashboard = () => {

  const { userId } = useAuth()
  const { setActive } = useOutletContext()
  const { data: dash, isLoading, isError, isSuccess, error } = useGetDashboardQuery(userId, {
    pollingInterval: 10 * 60 * 1000,
    refetchOnMountOrArgChange: true,
  })

  useEffect(() => {
    setActive(prev => 'dashboard')
  }, [setActive])


  let content
  if (isLoading) content = <p>Loading...</p>

  if (isError) {
    content = <p className="error-message">{error?.data?.message}</p>
  }

  if (isSuccess) {
    const { activeClass, invProfit, invAmount, invPlan, invPackage, percentage, referralLink } = getDashData(dash)

    content = <section className="section dashboard">
      <div className="row">

        {/* <!-- Left side columns --> */}
        <div className="col-lg-12">
          <div className="row">

            {/* <!-- Sales Card --> */}
            <div className="col-xxl-4 col-md-6">
              <div className="card info-card sales-card">

                <div className="card-body">
                  <h5 className="card-title">Investment Amount</h5>

                  <div className="d-flex align-items-center">
                    <div className="card-icon rounded-circle d-flex align-items-center justify-content-center">
                      <i className="bi bi-currency-dollar"></i>
                    </div>
                    <div className="ps-3">
                      <h6>{invAmount ?? '0'}</h6>
                    </div>
                  </div>
                </div>

              </div>
            </div>
            {/* <!-- End Sales Card --> */}

            {/* <!-- Revenue Card --> */}
            <div className="col-xxl-4 col-md-6">
              <div className="card info-card revenue-card">

                <div className="card-body">
                  <h5 className="card-title">Investment Profit <span>| This Week</span></h5>

                  <div className="d-flex align-items-center">
                    <div className="card-icon rounded-circle d-flex align-items-center justify-content-center">
                      <i className="bi bi-currency-dollar"></i>
                    </div>
                    <div className="ps-3">
                      <h6>{invProfit ?? '0'}</h6>
                      <span className="text-success small pt-1 fw-bold">{percentage}</span> <span className="text-muted small pt-2 ps-1">daily increase</span>
                    </div>
                  </div>
                </div>

              </div>
            </div>
            {/* <!-- End Revenue Card --> */}

            {/* <!-- Revenue Card --> */}
            <div className="col-xxl-4 col-md-6">
              <div className="card info-card revenue-card">

                <div className="card-body">
                  <h5 className="card-title">Referal Bonus <span>| This Year</span></h5>

                  <div className="d-flex align-items-center">
                    <div className="card-icon rounded-circle d-flex align-items-center justify-content-center">
                      <i className="bi bi-currency-dollar"></i>
                    </div>
                    <div className="ps-3">
                      <h6>{dash.referralBonus ?? dash.referrals * 250}</h6>
                      {/* <!-- <span className="text-success small pt-1 fw-bold">5%</span> <span className="text-muted small pt-2 ps-1">increase daily</span> --> */}

                    </div>
                  </div>
                </div>

              </div>

            </div>
            {/* <!-- End Revenue Card --> */}
            <div className="col-xxl-4 col-md-6">
              <div className="card info-card revenue-card">

                <div className="card-body">
                  <h5 className="card-title">Referrals</h5>

                  <div className="d-flex align-items-center">
                    <div className="card-icon rounded-circle d-flex align-items-center justify-content-center">
                      <i className="bi bi-people"></i>
                    </div>
                    <div className="ps-3">
                      <h6>{dash.referrals ?? 0}</h6>
                      <span className="text-success small pt-1 fw-bold">Referal link:</span>
                      <span id="copy-text" className="text-muted me-1 small pt-2 ps-1">{referralLink ?? 'N/A'}</span>
                      <button className="btn btn-outline-secondary action-btn" data-clipboard-target="#copy-text" title='Copy to clipboard'><i className="bi bi-clipboard"></i></button>
                    </div>
                  </div>
                </div>

              </div>
            </div>
            {/* <!-- End Revenue Card --> */}

            {/* <!-- Customers Card --> */}
            <div className="col-xxl-4 col-xl-12">

              <div className="card info-card customers-card">

                <div className="card-body">
                  <h5 className="card-title">Active Investment <span>| {invPlan ?? 'N/A'}</span></h5>

                  <div className="align-items-center">
                    <div className="ps-3">
                      <h5 className="pt-1 fw-bold">Package: {invPackage ?? 'N/A'}</h5>
                    </div>
                    <div className="ps-3">
                      <h5 className="pt-1 fw-bold">Status: <span className={`badge ${activeClass} text-white`}>{dash.investment?.[0].status ?? 'N/A'}</span></h5>
                    </div>
                  </div>

                </div>
              </div>

            </div>
            {/* <!-- End Customers Card --> */}
          </div>
        </div>
        {/* <!-- End Left side columns --> */}


      </div>
      <coingecko-coin-list-widget coin-ids="bitcoin,ethereum,eos,ripple,litecoin" currency="usd" locale="en"></coingecko-coin-list-widget>
    </section>
  }

  return (
    <>
      <div className="pagetitle">
        <h1>Dashboard</h1>
      </div>

      {content}
    </>
  )
}

export default Dashboard