import React, { useRef } from 'react'
import { useSelector } from 'react-redux'
import { useNavigate, useOutletContext } from 'react-router-dom'
import { useResetPasswordMutation } from '../../app/api/apiSlice'
import { selectDashboard } from './dashSlice'

const ChangePasswordForm = () => {

    const currentPassword = useRef()
    const newPassword = useRef()
    const rePassword = useRef()
    const navigate = useNavigate()
    const [resetPassword, { isLoading }] = useResetPasswordMutation()

    const { setShow, setVariant, setTitle, setBody } = useOutletContext()

    const dashboard = useSelector(state => selectDashboard(state))

    const handleChangePassword = async (e) => {
        e.preventDefault()
        const payload = {
            email: dashboard?.owner.email,
            currentPassword: currentPassword.current.value,
            newPassword: newPassword.current.value,
            rePassword: rePassword.current.value
        }
        try {
            const { message } = await resetPassword(payload).unwrap()
            setShow(true)
            setVariant('success')
            setTitle('SUCCESS')
            setBody(message)
            setTimeout(() => { navigate('/login') }, 3000)
        } catch (error) {
            setShow(true)
            setVariant('warning')
            setTitle('ERROR')
            setBody(error.data.message)
        }
    }

    return (
        <form onSubmit={handleChangePassword}>
            <div className="row mb-3">
                <div className="col-md-8 col-lg-12 form-floating">
                    <input ref={currentPassword} name="password" type="password" className="form-control" id="currentPassword" placeholder='Current Password' />
                    <label htmlFor="currentPassword" className="col-md-4 col-lg-3 col-form-label">Current Password</label>
                </div>
            </div>

            <div className="row mb-3">
                <div className="col-md-8 col-lg-12 form-floating">
                    <input ref={newPassword} name="newpassword" type="password" className="form-control" id="newPassword" placeholder='New Password' />
                    <label htmlFor="newPassword" className="col-md-4 col-lg-3 col-form-label">New Password</label>
                </div>
            </div>

            <div className="row mb-3 form-floating">
                <div className="col-md-8 col-lg-12 form-floating">
                    <input ref={rePassword} name="renewpassword" type="password" className="form-control" id="renewPassword" placeholder='Re-enter New Password' />
                    <label htmlFor="renewPassword" className="col-md-4 col-lg-3 col-form-label">Re-enter New Password</label>
                </div>
            </div>

            <div className="text-center">
                <button disabled={isLoading} type="submit" className="btn btn-dark w-100">Change Password</button>
            </div>
        </form>
    )
}

export default ChangePasswordForm