import React from "react"
import { Route, Routes } from "react-router-dom"

import AdminLayout from "./components/AdminLayout"
import DashLayout from "./components/DashLayout"
import PublicLayout from "./components/PublicLayout"

import Prefetch from "./features/auth/Prefetch"
import RequireAuth from "./features/auth/RequireAuth"
import UserPrefetch from "./features/auth/UserPrefetch"
import PersistLogin from "./features/auth/PersistLogin"

import Login from "./features/auth/Login"
import Signup from "./features/auth/Signup"
import ForgotPassword from "./components/ForgotPassword"
import ResetPassword from "./components/ResetPassword"

import AdminDashboard from "./features/dashboard/AdminDashboard"
import Dashboard from "./features/dashboard/Dashboard"
import Profile from "./features/dashboard/Profile"
import WithdrawForm from "./components/WithdrawForm"
import History from "./features/investment/History"
import DepositForm from "./features/deposit/DepositForm"
import CheckoutForm from "./features/deposit/CheckoutForm"
import Payment from "./features/deposit/Payment"


function App() {

  return (
    <Routes>
      {/* PUBLIC ROUTES */}
      <Route element={<PublicLayout />}>
        <Route path="/login" element={<Login />} />
        <Route path="/signup" element={<Signup />} />
        <Route path="/signup/:referrer" element={<Signup />} />
        <Route path="/forgot-password" element={<ForgotPassword />} />
        <Route path="/reset-password/:token" element={<ResetPassword />} />
      </Route>

      {/* PROTECTED ROUTES */}
      <Route element={<PersistLogin />}>
        <Route element={<RequireAuth allowedRoles={['USER']} />}>
          <Route element={<UserPrefetch />}>
            <Route path="/" element={<DashLayout />}>
              <Route index element={<Dashboard />} />
              <Route path="account-profile" element={<Profile />} />
              <Route path="history" element={<History />} />
              <Route path="withdraw-funds" element={<WithdrawForm />} />
              <Route path="deposit-funds" element={<DepositForm />} />
              <Route path="checkout" element={<CheckoutForm />} />
              <Route path="payment" element={<Payment />} />
            </Route>
          </Route>
        </Route>

        <Route element={<RequireAuth allowedRoles={['ADMIN']} />}>
          <Route element={<Prefetch />}>
            <Route path="/admin" element={<AdminLayout />}>
              <Route index element={<AdminDashboard />} />
              <Route path="profile/:id" element={<Profile />} />
            </Route>
          </Route>
        </Route>
      </Route>
    </Routes>
  )
}

export default App
