import { apiSlice } from "../../app/api/apiSlice"

export const depositApiSlice = apiSlice.injectEndpoints({
    endpoints: builder => ({
        makeDeposit: builder.mutation({
            query: ({ payload }) => ({
                url: `/investment/deposit`,
                method: 'POST',
                body: payload
            }),
            invalidatesTags: (result, error, { dashId }) => [
                { type: 'Dashboard', id: dashId },
                { type: 'Investment', id: 'List' }
            ],
        }),
        verifyDeposit: builder.mutation({
            query: ({ transId }) => ({
                url: `/investment/verify-deposit`,
                method: 'PATCH',
                body: { transId }
            }),
            invalidatesTags: (result, error, { dashId }) => [
                { type: 'Dashboard', id: dashId },
                { type: 'Investment', id: 'List' }
            ],
        }),
    }),
})

export const {
    useMakeDepositMutation,
    useVerifyDepositMutation
} = depositApiSlice
