import React, { useEffect, useRef, useState } from 'react'
import { useOutletContext } from 'react-router-dom'

const WithdrawForm = () => {
  const payAmount = useRef()
  const payAddress = useRef()
  const payMethod = useRef()

  const [valid, setValid] = useState(false)

  const { setActive } = useOutletContext()

  useEffect(() => {
    setActive(prev => 'withdrawal')
  }, [setActive])

  function checkValid(params) {
    if (payAddress.current.value && payAmount.current.value >= 600 && payMethod.current.value) {
      setValid(true)
    } else {
      setValid(false)
    }
  }

  return (
    <>
      <div className="pagetitle">
        <h1>Withdraw funds</h1>
      </div>
      {/* <!-- End Page Title --> */}

      <section className="section">
        <div className="app-content pt-3 p-md-3 p-lg-4">
          <div className="container-xl">
            <div className="row g-4">
              <div className="col-12 col-md-4">
                <h6>Available amount: $0</h6>
                <div>To withdraw funds, enter the amount and payment method and proceed to process the withdrawal.</div>
              </div>
              <div className="col-12 col-md-8">
                <div className="app-card app-card-settings shadow-sm p-4">

                  <div className="app-card-body">
                    <form>
                      <div className="mb-3">
                        <label htmlFor="setting-input-1" className="form-label">Enter the amount</label>
                        <input
                          onKeyUp={checkValid}
                          ref={payAmount} type="number" className="form-control" placeholder="Amount in USD($)" required />
                      </div>
                      <div className="mb-3">
                        <label htmlFor="payment-method" className="form-label">Select a payment method</label>
                        <select
                          onChange={checkValid}
                          ref={payMethod} id="paymetho-method" className="form-select">
                          <option defaultValue value="">Click to select</option>
                          <option value="btc">Bitcoin</option>
                          <option value="eth">Ethereum</option>
                          <option value="usdt">USDT</option>
                        </select>
                      </div>
                      <div className="mb-3">
                        <label htmlFor="wallet-address" className="form-label">Enter wallet address</label>
                        <input
                          onKeyUp={checkValid}
                          ref={payAddress} type="text" className="form-control" id="wallet-address" required />
                      </div>
                      <button
                        onClick={(e) => { e.preventDefault() }}
                        // disabled={!valid}
                        disabled={true}
                        type="submit" className="w-100 btn btn-dark" data-bs-toggle="modal"
                        data-bs-target="#basicModal">Confirm
                      </button>
                      <div className="modal fade" id="basicModal" tabIndex="-1">
                        <div className="modal-dialog">
                          <div className="modal-content">
                            <div className="modal-header">
                              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                            </div>
                            <div className="modal-body">
                              <div className="text-center">
                                <img src="assets/img/success.gif" className="success-img" alt="" />
                                <p>Your withdrawal is being processed, you will be credited upon confirmation</p>
                              </div>
                            </div>
                            <div className="modal-footer">
                              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                            </div>
                          </div>
                        </div>
                      </div>
                      {/* <!-- End Basic Modal--> */}
                    </form>
                  </div>

                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  )
}

export default WithdrawForm