import React, { useEffect } from 'react'
import { Outlet } from 'react-router-dom'
import { store } from '../../app/store'
import { dashboardApiSlice } from '../dashboard/dashboardApiSlice'
import { walletApiSlice } from '../dashboard/walletApiSlice'

const Prefetch = () => {
    useEffect(() => {
        console.log('Subscribing')
        const dash = store.dispatch(dashboardApiSlice.endpoints.getDashboards.initiate())
        const wallets = store.dispatch(walletApiSlice.endpoints.getWallets.initiate())

        return () => {
            console.log('Unubscribing')
            dash.unsubscribe()
            wallets.unsubscribe()
        }
    }, [])

    return <Outlet />
}

export default Prefetch