import { useRef, useState, useEffect } from 'react'
import { useNavigate, Link } from 'react-router-dom'

import { useDispatch } from 'react-redux'
import { setCredentials } from './authSlice'
import { useLoginMutation } from './authApiSlice'

const Login = () => {
  const mailRef = useRef()
  const errRef = useRef()
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [errMsg, setErrMsg] = useState('')
  const [remember, setRemember] = useState(false)

  const navigate = useNavigate()
  const dispatch = useDispatch()

  const [login, { isLoading }] = useLoginMutation()

  useEffect(() => {
    mailRef.current.focus()
  }, [])

  useEffect(() => {
    setErrMsg('');
  }, [email, password])

  const handleSubmit = async (e) => {
    e.preventDefault()
    try {
      const { accessToken, userId, role } = await login({ email, password }).unwrap()
      dispatch(setCredentials({ accessToken, userId, role }))
      setEmail('')
      setPassword('')
      if (role === 'ADMIN') {
        navigate('/admin')
      } else {
        navigate('/')
      }
    } catch (err) {
      if (!err.status) {
        setErrMsg('No Server Response');
      } else if (err.status === 400) {
        setErrMsg('Missing Email or Password');
      } else if (err.status === 401) {
        setErrMsg('Unauthorized');
      } else {
        setErrMsg(err.data?.message);
      }
      errRef.current.focus();
    }
  }

  const handleEmailInput = (e) => setEmail(e.target.value)
  const handlePwdInput = (e) => setPassword(e.target.value)
  const handleToggle = (e) => setRemember(prev => !prev)

  const errClass = errMsg ? "errmsg" : "offscreen"

  return (
    <>
      <div className="pt-4 pb-2 mb-3">
        <h5 className="card-title text-center pb-0 fs-4">Login to Your Account</h5>
      </div>
      <form className="row g-3 needs-validation" onSubmit={handleSubmit}>
        <p ref={errRef} className={errClass} aria-live="assertive">{errMsg}</p>

        <div className="col-12 form-floating">
          <input ref={mailRef} onChange={handleEmailInput} value={email} type="email" name="email" className="form-control" id="yourEmail" placeholder="Email Address" required />
          <label htmlFor="yourEmail" className="form-label">Email Address</label>
          <div className="invalid-feedback">Please enter your Email.</div>
        </div>

        <div className="col-12 form-floating">
          <input onChange={handlePwdInput} value={password} type="password" name="password" className="form-control" id="yourPassword" placeholder="Password" required />
          <label htmlFor="yourPassword">Password</label>
          <div className="invalid-feedback">Please enter your password!</div>
          <Link className="small mb-0" to="/forgot-password">Forgot Password?</Link>
        </div>

        <div className="col-12">
          <div className="form-check">
            <input onChange={handleToggle} checked={remember} className="form-check-input" type="checkbox" name="remember" value="true" id="rememberMe" />
            <label className="form-check-label" htmlFor="rememberMe">Remember me</label>
          </div>
        </div>

        <div className="col-12">
          <button className="btn btn-dark w-100 mb-3" type="submit" disabled={isLoading}>
            {isLoading ? 'Loading...' : 'Login'}
          </button>
          <button className="btn btn-outline-dark w-100" disabled={isLoading}>
            <span><i className="bi bi-google"></i> </span>Login with Google
          </button>
        </div>

        <div className="col-12">
          <p className="small mb-0">Don't have account? <Link to="/signup">Create an account</Link></p>
        </div>
      </form>
    </>
  )
}

export default Login