import React, { useState } from 'react'
import { useOutletContext } from 'react-router-dom'
import { useGetResetPasswordMailMutation } from '../app/api/apiSlice'

const ForgotPassword = () => {
  const [email, setEmail] = useState('')

  const { setTitle, setBody, setVariant, setShow } = useOutletContext()

  const [getPasswordMailMutation, { isLoading }] = useGetResetPasswordMailMutation()

  const handleSubmit = async (e) => {
    e.preventDefault()
    try {
      const { message } = await getPasswordMailMutation(email).unwrap()
      setEmail('')
      setShow(true)
      setTitle('SUCCESS')
      setBody(message)
      setVariant('success')
    } catch (error) {
      setShow(true)
      setTitle('ERROR')
      setBody(error.data.message)
      setVariant('warning')
    }
  }

  return (
    <>
      <div className="pt-4 pb-2">
        <h5 className="card-title text-center pb-0 fs-4">Forgot Password?</h5>
        <p>Enter your email to get a link to reset your password</p>
      </div>
      <form onSubmit={handleSubmit} className="row g-3 needs-validation">
        <div className="col-12 form-floating">
          <input onChange={e => { setEmail(e.target.value) }} value={email} type="email" name="email" className="form-control" id="yourEmail" placeholder="Email Address" required />
          <label htmlFor="yourEmail" className="form-label">Email Address</label>
          <div className="invalid-feedback">Please enter your Email.</div>
        </div>
        <div className="col-12">
          <button disabled={isLoading} className="btn btn-dark w-100 mb-3" type="submit">Proceed</button>
        </div>
      </form>
    </>
  )
}

export default ForgotPassword