import React from 'react'

const WalletRow = ({ address, id, setWalletId }) => {
    return (
        <div className="d-flex justify-content-between wallet-add mb-3">
            <h6>{address.substring(0, 7)}****{address.substring(address.length - 7)}</h6>
            <button onClick={e => { setWalletId(prev => id) }} title='Delete wallet' className='btn btn-outline-danger action-btn ml-3' data-bs-toggle="modal" data-bs-target="#deleteWallet">
                <i className="bi bi-trash"></i>
            </button>
        </div>
    )
}

export default WalletRow