import React, { useRef } from 'react'
import { useOutletContext } from 'react-router-dom'
import { useUploadKYCMutation } from '../app/api/apiSlice'
import countries from '../countries.json'
import useAuth from '../hooks/useAuth'

const KYCForm = () => {
    const country = useRef()
    const DOB = useRef()
    const idType = useRef()
    const frontID = useRef()
    const backID = useRef()
    const [uploadKYC, { isLoading }] = useUploadKYCMutation()
    const { userId } = useAuth()

    const { setShow, setVariant, setTitle, setBody } = useOutletContext()

    const countryOptions = Object.entries(countries).map(([short, long]) => {
        return <option key={short} value={short}>{long}</option>
    })

    const handleSubmit = async (e) => {
        e.preventDefault()
        const payload = new FormData()

        payload.append('country', country.current.value)
        payload.append('DOB', DOB.current.value)
        payload.append('IDType', idType.current.value)
        payload.append('ID', frontID.current.files[0])
        payload.append('ID', backID.current.files[0])

        try {
            const { message } = await uploadKYC({ userId, payload }).unwrap()

            setShow(true)
            setVariant('success')
            setTitle('SUCCESS')
            setBody(message)

            country.current.value = null
            DOB.current.value = null
            idType.current.value = null
            frontID.current.value = null
            backID.current.value = null
        } catch (error) {
            setShow(true)
            setVariant('warning')
            setTitle('ERROR')
            setBody(error.data.message)
        }
    }

    return (
        <form onSubmit={handleSubmit}>
            <div className="row mb-3">
                <div className="col-md-8 col-lg-12">
                    <select ref={country} className='form-select' required>
                        <option defaultValue value="">Country</option>
                        {countryOptions}
                    </select>
                </div>
            </div>

            <div className="row mb-3">
                <div className="col-md-8 col-lg-12 form-floating">
                    <input ref={DOB} name="dob" type="date" className="form-control" id="dob" max="1999-12-15" placeholder='Date of birth' required />
                    <label htmlFor="dob" className="col-md-4 col-lg-3 col-form-label">Date of birth</label>
                </div>
            </div>

            <div className="row mb-3 form-floating">
                <div className="col-md-8 col-lg-12 form-floating">
                    <input ref={idType} name="id" type="text" className="form-control" id="id" placeholder='ID type - passport, DL, etc' required />
                    <label htmlFor="id" className="form-label">ID type - passport, DL, etc</label>
                </div>
            </div>


            <div className="row mb-3">
                <div className="col md-8 col-lg-12">
                    <label htmlFor="frontID" className="form-label">Upload front of ID</label>
                    <input id='frontID' className="form-control" type="file" accept='image/*'
                        ref={frontID} required />
                </div>
            </div>

            <div className="row mb-3">
                <div className="col md-8 col-lg-12">
                    <label htmlFor="backID" className="form-label">Upload back of ID</label>
                    <input id='backID' className="form-control" type="file" accept='image/*'
                        ref={backID} required />
                </div>
            </div>

            <div className="text-center">
                <button disabled={isLoading} type="submit" className="btn btn-dark w-100">Submit</button>
            </div>
        </form>
    )
}

export default KYCForm