import React, { useEffect, useRef } from 'react'
import { useNavigate, useOutletContext, useParams } from 'react-router-dom'
import jwt_decode from "jwt-decode"
import { useResetPasswordMutation } from '../app/api/apiSlice'

const ResetPassword = () => {
  const { token } = useParams()
  const newPassword = useRef()
  const rePassword = useRef()
  const [resetPassword, { isLoading }] = useResetPasswordMutation()

  const { setTitle, setBody, setVariant, setShow } = useOutletContext()

  const navigate = useNavigate()

  useEffect(() => {
    newPassword.current.focus()
  }, [])

  const decoded = jwt_decode(token)

  const handleSubmit = async (e) => {
    e.preventDefault()
    const payload = {
      email: decoded?.email,
      currentPassword: decoded?.pass,
      newPassword: newPassword.current.value,
      rePassword: rePassword.current.value
    }
    try {
      const { message } = await resetPassword(payload).unwrap()
      setShow(true)
      setVariant('success')
      setTitle('SUCCESS')
      setBody(message)
      setTimeout(() => { navigate('/login') }, 3000)
    } catch (error) {
      setShow(true)
      setVariant('warning')
      setTitle('ERROR')
      setBody(error.data.message)
    }
  }

  return (
    <>
      <div className="pt-4 pb-2">
        <h5 className="card-title text-center pb-0 fs-4">Create new Password</h5>
      </div>

      <form onSubmit={handleSubmit} className="row g-3 needs-validation">

        <div className="col-12 form-floating">
          <input ref={newPassword} type="password" name="newPassword" className="form-control" id="newPassword" placeholder="New Password" required />
          <label htmlFor="yourEmail" className="form-label">New Password</label>
          <div className="invalid-feedback">Please enter your Password.</div>
        </div>

        <div className="col-12 form-floating">
          <input ref={rePassword} type="password" name="confirmPassword" className="form-control" id="confirmPassword" placeholder="Confirm Password" required />
          <label htmlFor="yourEmail" className="form-label">Confirm Password</label>
          <div className="invalid-feedback">Please confirm your Password.</div>
        </div>

        <div className="col-12">
          <button disabled={isLoading} className="btn btn-dark w-100 mb-3" type="submit">Submit</button>
        </div>
      </form>
    </>
  )
}

export default ResetPassword