import {
    createSelector,
    createEntityAdapter
} from "@reduxjs/toolkit";
import { apiSlice } from "../../app/api/apiSlice"
import { setDashboard } from "./dashSlice";

const dashboardAdapter = createEntityAdapter({})

const initialState = dashboardAdapter.getInitialState()

export const dashboardApiSlice = apiSlice.injectEndpoints({
    endpoints: builder => ({
        getDashboards: builder.query({
            query: () => '/dashboard',
            validateStatus: (response, result) => {
                return response.status === 200 && !result.isError
            },
            // keepUnusedDataFor: 5, //60 is default
            transformResponse: responseData => {
                const loadedDashboards = responseData.map(dashboard => {
                    dashboard.id = dashboard._id
                    return dashboard
                });
                return dashboardAdapter.setAll(initialState, loadedDashboards)
            },
            providesTags: (result, error, arg) => {
                if (result?.ids) {
                    return [
                        { type: 'Dashboard', id: 'LIST' },
                        ...result.ids.map(id => ({ type: 'Dashboard', id }))
                    ]
                } else return [{ type: 'Dashboard', id: 'LIST' }]
            }
        }),
        getDashboard: builder.query({
            query: (userId) => `/dashboard/${userId}`,
            // keepUnusedDataFor: 5, //60 is default
            transformResponse: responseData => {
                responseData.id = responseData._id
                // return dashboardAdapter.setOne(initialState, responseData)
                return responseData
            },
            providesTags: (result, error, id) => {
                console.log(id)
                return [{ type: 'Dashboard', id }]
            },
            async onQueryStarted(userId, { dispatch, queryFulfilled }) {
                try {
                    const { data } = await queryFulfilled
                    dispatch(setDashboard({ ...data }))
                } catch (err) {
                    console.log(err)
                }
            }
        }),
        deleteUser: builder.mutation({
            query: ({ userId }) => ({
                url: `user/${userId}`,
                method: 'DELETE'
            }),
            invalidatesTags: [{ type: 'Dashboard', id: 'LIST' }],
        }),
        verifyUser: builder.mutation({
            query: ({ userId }) => ({
                url: `user/verify/${userId}`,
                method: 'PATCH'
            }),
            invalidatesTags: (result, error, { userId }) => [{ type: 'Dashboard', id: userId }],
        }),
    }),
})

export const {
    useGetDashboardsQuery,
    useGetDashboardQuery,
    useDeleteUserMutation,
    useVerifyUserMutation,
} = dashboardApiSlice

// returns the query result object
export const selectDashboardsResult = dashboardApiSlice.endpoints.getDashboards.select()

// creates memoized selector
const selectDashboardsData = createSelector(
    selectDashboardsResult,
    dashboardsResult => dashboardsResult.data // normalized state object with ids & entities
)

//getSelectors creates these selectors and we rename them with aliases using destructuring
export const {
    selectAll: selectAllDashboards,
    selectById: selectDashboardById,
    selectIds: selectDashboardIds
    // Pass in a selector that returns the dashboards slice of state
} = dashboardAdapter.getSelectors(state => selectDashboardsData(state) ?? initialState)