import React, { useEffect } from 'react'
import { Outlet } from 'react-router-dom'
import { store } from '../../app/store'
import useAuth from '../../hooks/useAuth'
import { dashboardApiSlice } from '../dashboard/dashboardApiSlice'

const UserPrefetch = () => {
    const { userId } = useAuth()

    // useEffect(() => {
    //     console.log('Subscribing')
    //     store.dispatch(dashboardApiSlice.util.prefetch('getDashboard', userId, { force: true }))
    // }, [userId])

    useEffect(() => {
        console.log('Subscribing')
        const dash = store.dispatch(dashboardApiSlice.endpoints.getDashboard.initiate(userId))

        return () => {
            console.log('Unubscribing')
            dash.unsubscribe()
        }
    }, [userId])

    return <Outlet />
}

export default UserPrefetch