
const packageConverter = {
    'AGRICULTURE': 5,
    'FOREX': 2.5,
    'STOCK': 2.5,
    'INHERITANCE': 7.5,
    'ENERGY': 7.5,
    'CRYPTOCURRENCY': 2.5,
    'METAL': 7.5,
    'REAL_ESTATE': 5
}

export const getPercentage = (pckg) => {
    return packageConverter[pckg]
}

const shortenURL = (str) => {
    const first = str.substring(0, 9)
    const last = str.substring(str.length - 4)
    return `${first}...${last}`
}

export const getDashData = (dash) => {

    let activeClass
    let invProfit
    let invAmount
    let invPlan
    let invPackage
    let percentage
    let referralLink = shortenURL(dash.referralLink)

    if (dash.hasInvestment) {
        if (/(PENDING)|(INACTIVE)/.test(dash.investment?.[0].status)) {
            activeClass = 'bg-warning'
            invAmount = 'N/A'
            invProfit = 'N/A'
            invPlan = 'N/A'
            invPackage = 'N/A'
            percentage = 'N/A'
        } else {
            activeClass = 'bg-success'
            invAmount = dash.investment?.[0].investmentAmount
            invProfit = dash.investment?.[0].ROI
            invPlan = dash.investment?.[0].investmentPlan
            invPackage = dash.investment?.[0].investmentPackage
            percentage = `${getPercentage(dash.investment?.[0].investmentPackage)}%`
        }
    }

    return { activeClass, invProfit, invAmount, invPlan, invPackage, percentage, referralLink }
}