import React, { useRef, useState } from 'react'
import DashUserRow from './DashUserRow'
import { useGetDashboardsQuery } from './dashboardApiSlice'
import DeleteUserModal from './DeleteUserModal'
import { useAddWalletMutation, useDeleteWalletMutation, useGetWalletsQuery } from './walletApiSlice'
import { useOutletContext } from 'react-router-dom'
import WalletRow from './WalletRow'

const AdminDashboard = () => {

  const [userId, setUserId] = useState()
  const [walletId, setWalletId] = useState()
  const closeModal = useRef()
  const { setShow, setVariant, setTitle, setBody } = useOutletContext()
  const [addWallet, { isLoading: addLoading }] = useAddWalletMutation()
  const [deleteWallet, { isLoading: deleteLoading }] = useDeleteWalletMutation()
  const {
    data: wallets,
    isSuccess: iswalletsSuccess,
    // isLoading: iswalletsLoading,
    // isError: iswalletsError,
    // error: walletsError
  } = useGetWalletsQuery(undefined, {
    pollingInterval: 60000,
    refetchOnFocus: true,
    refetchOnMountOrArgChange: 60,
  })

  let btcWallets, ethWallets, usdtWallets

  if (iswalletsSuccess) {
    btcWallets = Object.values(wallets.entities).filter(({ type }) => type === 'btc').map(({ address, id }, idx) => <WalletRow key={idx} id={id} address={address} setWalletId={setWalletId} />)
    ethWallets = Object.values(wallets.entities).filter(({ type }) => type === 'eth').map(({ address, id }, idx) => <WalletRow key={idx} id={id} address={address} setWalletId={setWalletId} />)
    usdtWallets = Object.values(wallets.entities).filter(({ type }) => type === 'usdt').map(({ address, id }, idx) => <WalletRow key={idx} id={id} address={address} setWalletId={setWalletId} />)
  }

  const {
    data: dashboards,
    isLoading,
    isSuccess,
    isError,
    error
  } = useGetDashboardsQuery(undefined, {
    pollingInterval: 60000,
    refetchOnFocus: true,
    refetchOnMountOrArgChange: 60,
    // refetchOnReconnect: true
  })

  let content

  if (isLoading) content = <p>Loading...</p>

  if (isError) {
    content = <p className="error-message">{error?.data?.message}</p>
  }

  if (isSuccess) {
    const { ids } = dashboards

    const tableContent = ids.length
      ? ids.map(dashId => <DashUserRow key={dashId} dashId={dashId} setUserId={setUserId} />)
      : null

    content = (
      <div className="tab-content pt-2">
        <section className="tab-pane show active fade pt-3" id="user-magt">
          <div className="card overflow-auto">
            <table className="table table-borderless table-striped">
              <thead>
                <tr>
                  <th scope="col">Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Invt status</th>
                  <th scope="col" className='issues'>Issues</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>
              <tbody>
                {tableContent}
              </tbody>
            </table>
            <DeleteUserModal userId={userId} />
          </div>
        </section>
      </div>
    )
  }

  const handleAddWallet = async (e) => {
    e.preventDefault()
    try {
      const payload = new FormData()
      payload.append('type', e.target.wallet_type.value)
      payload.append('address', e.target.wallet_address.value)
      payload.append('barcode', e.target.barcode.files[0])

      const { message } = await addWallet({ payload }).unwrap()

      setVariant('success')
      setTitle('SUCCESS')
      setBody(message)
      setShow(true)

      e.target.wallet_type.value = null
      e.target.wallet_address.value = null
      e.target.barcode.value = null
    } catch (error) {
      setShow(true)
      setVariant('warning')
      setTitle('ERROR')
      setBody(error.data.message)
    }
  }

  const handleDeleteWallet = async (e) => {
    e.preventDefault()
    try {
      const { message } = await deleteWallet({ walletId }).unwrap()
      setVariant('success')
      setTitle('SUCCESS')
      setBody(message)
      setShow(true)

      closeModal.current.click()
      setWalletId(prev => '')
    } catch (error) {
      setShow(true)
      setVariant('warning')
      setTitle('ERROR')
      setBody(error.data.message)
    }
  }

  return (
    <>
      <div className="pagetitle">
        <ul className="nav nav-tabs nav-tabs-bordered">

          <li className="nav-item">
            <button className="nav-link active" data-bs-toggle="tab" data-bs-target="#user-magt">User Management</button>
          </li>
          <li className="nav-item">
            <button className="nav-link" data-bs-toggle="tab" data-bs-target="#wallet-magt">Wallet Management</button>
          </li>
        </ul>
      </div>
      {content}
      <div className="tab-pane fade pt-3" id="wallet-magt">
        <div className="add-wallet m-auto w-50 text-center">
          <h3>Add a new Wallet</h3>
          <form className='m-3' onSubmit={handleAddWallet}>
            <select className='form-control form-select mb-3' id='wallet_type' name='wallet_type' required>
              <option defaultValue=''>Select Wallet type</option>
              <option value='btc'>BTC</option>
              <option value='eth'>ETH</option>
              <option value='usdt'>USDT</option>
            </select>
            <input className='form-control mb-3' type='text' name='wallet_address' placeholder='Wallet address' required />
            <input className='form-control' name='barcode' type='file' accept='image/*' required />
            <p className='sm'>**Upload the QR code here</p>
            <button disabled={addLoading} type='submit' className="btn btn-outline-secondary">
              {addLoading ? 'Processing...' : 'Add wallet'}
            </button>
          </form>
        </div>

        <section>
          <div className="row">
            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">BTC Wallets</h5>
                  {btcWallets}
                </div>
              </div>
            </div>

            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">ETH Wallets</h5>
                  {ethWallets}
                </div>
              </div>
            </div>

            <div className="col-lg-4">
              <div className="card">
                <div className="card-body">
                  <h5 className="card-title">USDT Wallets</h5>
                  {usdtWallets}
                </div>
              </div>
            </div>
          </div>

          <div className="modal fade" id="deleteWallet" tabIndex="-1">
            <div className="modal-dialog modal-dialog-centered">
              <div className="modal-content">
                <div className="modal-body text-center">
                  You are about to delete this wallet permanently. Proceed?
                </div>
                <div className="modal-footer">
                  <button ref={closeModal} disabled={deleteLoading} type="button" className="btn btn-secondary action-btn" data-bs-dismiss="modal">Close</button>
                  <button disabled={deleteLoading} onClick={handleDeleteWallet} type="button" className="btn btn-danger action-btn">
                    {deleteLoading ? 'Processing...' : 'Confirm'}
                  </button>
                </div>
              </div>
            </div>
          </div>

        </section>
      </div>
    </>
  )
}

export default AdminDashboard