import React from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { selectDashboardById } from './dashboardApiSlice'

const DashUserRow = ({ dashId, setUserId }) => {
    const dash = useSelector(state => selectDashboardById(state, dashId))

    return (
        <tr>
            <td>{dash.owner.firstName} {dash.owner.lastName}</td>
            <td>{dash.owner.email}</td>
            <td>{dash.hasInvestment ? 'Active' : 'Inactive'}</td>
            <td>KYC Verification</td>
            <td>
                <div className="action-btns">
                    <Link to={`/admin/profile/${dashId}`} title='Go to user profile' className='btn btn-outline-secondary action-btn'><i className="bi bi-box-arrow-up-right"></i></Link>
                    <button onClick={e => { setUserId(prev => dash.owner.id) }} title='Delete user permanently' className='btn btn-outline-danger action-btn' data-bs-toggle="modal" data-bs-target="#deleteAction"><i className="bi bi-trash"></i></button>
                </div>
            </td>
        </tr>
    )
}

export default DashUserRow