import React, { useRef } from 'react'
import { useOutletContext } from 'react-router-dom'
import { useDeleteUserMutation } from './dashboardApiSlice'

const DeleteUserModal = ({ userId }) => {

    const [deleteUser, { isLoading }] = useDeleteUserMutation()
    const closeModal = useRef()

    const { setShow, setVariant, setTitle, setBody } = useOutletContext()

    const handleDelete = async (e) => {
        try {
            const { message } = await deleteUser({ userId }).unwrap()
            closeModal.current.click()

            setShow(true)
            setVariant('success')
            setTitle('SUCCESS')
            setBody(message)
        } catch (error) {
            setShow(true)
            setVariant('warning')
            setTitle('ERROR')
            setBody(error.data.message)
        }
    }
    return (
        <div className="modal fade" id="deleteAction" tabIndex="-1">
            <div className="modal-dialog modal-dialog-centered">
                <div className="modal-content">
                    <div className="modal-body text-center">
                        You are about to delete the user permanently. Proceed?
                    </div>
                    <div className="modal-footer">
                        <button disabled={isLoading} ref={closeModal} type="button" className="btn btn-secondary action-btn" data-bs-dismiss="modal">Close</button>
                        <button disabled={isLoading} onClick={handleDelete} type="button" className="btn btn-danger action-btn">Confirm</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default DeleteUserModal