import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import useAuth from '../../hooks/useAuth'
import { useSendLogoutMutation } from '../auth/authApiSlice'
import { selectDashboard } from './dashSlice'

const DashHeader = ({ toggleSidebar }) => {

    const navigate = useNavigate()
    const { isAdmin } = useAuth()
    const dash = useSelector(state => selectDashboard(state))
    const { role } = useAuth()

    let fullName, shortName, title
    title = (role === 'ADMIN') ? 'Administrator' : 'Investor'
    if (dash) {
        fullName = `${dash.owner.firstName} ${dash.owner.lastName}`
        shortName = `${dash.owner.firstName[0]}. ${dash.owner.lastName}`
    } else {
        fullName = `ADMIN`
        shortName = `ADMIN`
    }

    const [sendLogout, {
        isLoading,
        isSuccess,
        isError,
        error
    }] = useSendLogoutMutation()

    useEffect(() => {
        if (isSuccess) navigate('/login')
    }, [isSuccess, navigate])

    if (isLoading) return <p>Logging Out...</p>

    if (isError) return <p>Error: {error.data?.message}</p>

    return (
        <header id="header" className="header fixed-top d-flex align-items-center">

            <div className="d-flex align-items-center justify-content-between">
                <Link to="/" className="logo d-flex align-items-center">
                    <img src="/assets/img/logo-blue.png" alt="" width='36px' height='36px' />
                    <span className="d-none d-lg-block">Marstrade</span>
                </Link>
                {!isAdmin && <i onClick={() => {
                    toggleSidebar()
                }} className="bi bi-list toggle-sidebar-btn"></i>}
            </div>
            {/* <!-- End Logo --> */}

            <nav className="header-nav ms-auto">
                <div className="nav-item dropdown pe-3">
                    <Link className="nav-link nav-profile d-flex align-items-center pe-0" to="#" data-bs-toggle="dropdown">
                        <img src={dash?.avatar ?? "/assets/img/no-avatar.png"} alt="Profile" className="rounded-circle" width='36px' height='36px' />
                        <span className="d-none d-md-block dropdown-toggle ps-2">{shortName}</span>
                    </Link>
                    {/* <!-- End Profile Iamge Icon --> */}
                    <ul className="dropdown-menu dropdown-menu-end dropdown-menu-arrow profile">
                        <li className="dropdown-header">
                            <h6>{fullName}</h6>
                            <span>{title}</span>
                        </li>
                        <li>
                            <hr className="dropdown-divider" />
                        </li>

                        {!isAdmin && <>
                            <li>
                                <Link className="dropdown-item d-flex align-items-center" to="/account-profile">
                                    <i className="bi bi-person"></i>
                                    <span>Profile</span>
                                </Link>
                            </li>
                            <li>
                                <hr className="dropdown-divider" />
                            </li>
                            <li>
                                <Link className="dropdown-item d-flex align-items-center" to="pages-faq.html">
                                    <i className="bi bi-question-circle"></i>
                                    <span>Need Help?</span>
                                </Link>
                            </li>
                            <li>
                                <hr className="dropdown-divider" />
                            </li>
                        </>}

                        <li>
                            <button onClick={sendLogout} className="dropdown-item d-flex align-items-center">
                                <i className="bi bi-box-arrow-right"></i>
                                <span>Sign Out</span>
                            </button>
                        </li>

                    </ul>
                    {/* <!-- End Profile Dropdown Items --> */}
                </div>
                {/* <!-- End Profile Nav --> */}
            </nav>
            {/* <!-- End Icons Navigation --> */}
        </header >
    )
}

export default DashHeader