import React, { useRef } from 'react'
import { useOutletContext } from 'react-router-dom'
import { useUpdateAvatarMutation } from '../app/api/apiSlice'
import useAuth from '../hooks/useAuth'

const AvatarForm = () => {

    const closeModal = useRef()
    const avatar = useRef()
    const { userId } = useAuth()
    const [updateAvatar, { isLoading }] = useUpdateAvatarMutation()

    const { setShow, setVariant, setTitle, setBody } = useOutletContext()

    const handleSubmit = async (e) => {
        e.preventDefault()

        const payload = new FormData()
        payload.append('avatar', avatar.current.files[0])

        try {
            const { message } = await updateAvatar({ userId, payload }).unwrap()
            avatar.current.value = null
            closeModal.current.click()

            setShow(true)
            setVariant('success')
            setTitle('SUCCESS')
            setBody(message)
        } catch (error) {
            setShow(true)
            setVariant('warning')
            setTitle('ERROR')
            setBody(error.data.message)
        }
    }

    return (
        <div className="modal fade" id="basicModal" tabIndex="-1">
            <div className="modal-dialog">
                <div className="modal-content">
                    <form onSubmit={handleSubmit}>
                        <div className="modal-header">
                            <h5 className="modal-title">Change photo</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                        </div>
                        <div className="modal-body">
                            <div className="row mb-3">
                                <div className="col-sm-10">
                                    <input className="form-control" type="file" accept='image/*'
                                        ref={avatar} required />
                                </div>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button ref={closeModal} type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                            <button disabled={isLoading} type="submit" className="btn btn-primary">Save changes</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default AvatarForm