import {
    createSelector,
    createEntityAdapter
} from "@reduxjs/toolkit"
import { apiSlice } from "../../app/api/apiSlice"

const walletAdapter = createEntityAdapter({})

const initialState = walletAdapter.getInitialState()

export const walletApiSlice = apiSlice.injectEndpoints({
    endpoints: builder => ({
        getWallets: builder.query({
            query: () => '/wallet',
            validateStatus: (response, result) => {
                return response.status === 200 && !result.isError
            },
            transformResponse: responseData => {
                const loadedWallets = responseData.map(wallet => {
                    wallet.id = wallet._id
                    return wallet
                })
                return walletAdapter.setAll(initialState, loadedWallets)
            },
            providesTags: (result, error, arg) => {
                if (result?.ids) {
                    return [
                        { type: 'Wallet', id: 'LIST' },
                        ...result.ids.map(id => ({ type: 'Wallet', id }))
                    ]
                } else return [{ type: 'Wallet', id: 'LIST' }]
            }
        }),
        addWallet: builder.mutation({
            query: ({ payload }) => ({
                url: `/wallet`,
                method: 'POST',
                body: payload
            }),
            invalidatesTags: [{ type: 'Wallet', id: 'LIST' }]
        }),
        deleteWallet: builder.mutation({
            query: ({ walletId }) => ({
                url: `/wallet`,
                method: 'DELETE',
                body: { walletId }
            }),
            invalidatesTags: [{ type: 'Wallet', id: 'LIST' }]
        }),
        updateDashWallet: builder.mutation({
            query: ({ payload }) => ({
                url: `/wallet`,
                method: 'PATCH',
                body: payload
            }),
            invalidatesTags: [{ type: 'Wallet', id: 'LIST' }]
        }),
    }),
})

export const {
    useGetWalletsQuery,
    useAddWalletMutation,
    useDeleteWalletMutation,
    useUpdateDashWalletMutation,
} = walletApiSlice

// returns the query result object
export const selectWalletsResult = walletApiSlice.endpoints.getWallets.select()

// creates memoized selector
const selectWalletsData = createSelector(
    selectWalletsResult,
    walletsResult => walletsResult.data // normalized state object with ids & entities
)

//getSelectors creates these selectors and we rename them with aliases using destructuring
export const {
    selectAll: selectAllWallets,
    selectById: selectWalletById,
    selectIds: selectWalletIds
    // Pass in a selector that returns the wallets slice of state
} = walletAdapter.getSelectors(state => selectWalletsData(state) ?? initialState)