import React, { useRef, useState } from 'react'
import { Outlet } from 'react-router-dom'
import DashHeader from '../features/dashboard/DashHeader'
import DashFooter from './DashFooter'
import Sidebar from './Sidebar'
import ToastComponent from './ToastComponent'

const DashLayout = () => {

    const [title, setTitle] = useState('')
    const [body, setBody] = useState('')
    const [variant, setVariant] = useState('')
    const [show, setShow] = useState(false)

    const [active, setActive] = useState('')

    const container = useRef()

    const toggleSidebar = () => {
        container.current.classList.toggle('toggle-sidebar')
    }

    return (
        <div className='app-body' ref={container}>
            <DashHeader toggleSidebar={toggleSidebar} />
            <Sidebar active={active} />
            <div className="my-flex">
                <ToastComponent show={show} setShow={setShow} title={title} body={body} variant={variant} />
                <main id="main" className="main ">
                    <Outlet context={{ setActive, setTitle, setBody, setVariant, setShow }} />
                </main>
                <DashFooter />
            </div>
        </div>
    )
}

export default DashLayout